import React from 'react'
import { makeStyles } from '@material-ui/core/styles';
import Card from '@material-ui/core/Card';
import CardContent from '@material-ui/core/CardContent';
import Typography from '@material-ui/core/Typography';


const useStyles = makeStyles({
    root: {
        minWidth: 275,
        backgroundColor: '#2e343b',
        textAlign: 'center',
        margin: '1em'
    },
    title: {
        fontWeight: '500',
        textTransform: 'capitalize'
    },
    subTitle: {
        color: '#e1e2e6',
        textTransform: 'capitalize'
    },
});

function CardCovid(props) {
    const classes = useStyles();

    return (
        <Card className={classes.root}>
            <CardContent>
                <Typography variant="h2" className={classes.title} style={{ color: props.color }} gutterBottom>
                    {props.title}
                </Typography>
                <Typography variant="h5" className={classes.subTitle} component="h2">
                    {props.subTitle}
                </Typography>
            </CardContent>
        </Card>
    );
}

export default CardCovid